import { _decorator, Component, Sprite, SpriteFrame, resources, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('MahjongGenerator')
export class MahjongGenerator extends Component {
    async onLoad() {
        // 加载图片
        const spriteFrame = await this.loadSpriteFrame('Tiles/mj_wan_1');
        if (!spriteFrame) return;

        // 创建 Sprite 节点
        const tileNode = new Node('Tile');
        const sprite = tileNode.addComponent(Sprite);
        sprite.spriteFrame = spriteFrame;
        this.node.addChild(tileNode);
    }

    private loadSpriteFrame(path: string): Promise<SpriteFrame | null> {
        return new Promise((resolve) => {
            resources.load(path, SpriteFrame, (err, spriteFrame) => {
                if (err) {
                    console.error('加载失败:', path, err);
                    resolve(null);
                } else {
                    resolve(spriteFrame);
                }
            });
        });
    }
}